  // x_token=localStorage.getItem('x-token')||'';
  headers = new HttpHeaders({
    // "x-token":this.x_token,
    'Accept':'application/json',
    'Access-Control-Allow-Origin': '*'
});
  constructor(private _httpClient: HttpClient,private router:Router,public jwtHelper: JwtHelperService,public dialog: MatDialog,) 
  {
    this.onContactChanged = new BehaviorSubject([]);
    this.user=new BehaviorSubject([])
   }

   login(contact:any){

    return this._httpClient.post<any>("http://localhost:3000/user/login",contact).pipe(
      map (res=>{
      return res
    }))
    
   
  }

  // islogged(){
   
