import { validateHorizontalPosition } from '@angular/cdk/overlay';
import { Component, OnInit } from '@angular/core';
import { Form, FormBuilder, FormControl, FormControlName, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';
import { Contact } from '../contact.model';
import { LoginComponent } from '../login/login.component';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  registerForm:any;
  regAuth:any;
  contact:Contact=new Contact({});
  

  constructor(private form:FormBuilder,private authService:AuthService,private _snackBar: MatSnackBar,private router:Router) { 
    
  }

  ngOnInit(): void {
    this.registerForm=this.createFormRegister()
  }

  createFormRegister(): FormGroup{
return this.form.group({
  username:new FormControl(''),
  firstName: new FormControl('', Validators.required),
  lastName:new FormControl('',Validators.required),
  email:new FormControl('',Validators.required),
  password:new FormControl('',[Validators.required,Validators.min(5)]),
  adresse:new FormControl(''),
  city:new FormControl(''),
  pays:new FormControl(''),
  code_postal:new FormControl(''),
  roles:new FormControl(['Admin']),
  phone:new FormControl('')
})
  }



  register():void{
    console.log(this.registerForm.value)
    this.contact=this.registerForm.value;
    this.regAuth=this.authService.register(this.contact).then(res=>{
    if (!res.status){
      this.openSnackBar('vérifier les champs')
    }else {
      this.openSnackBar('register avec succés')
      this.authService.closedialog()
      this.router.navigateByUrl('/dashboad/myadverts/:iduser'+this.regAuth.resultat.id)
    }
  })

  }



  openSnackBar(action:any) {
    this._snackBar.open('',action,
      {
        duration:5000
      });
  }

  openLogin(): void {
    this.authService.closedialog();
    this.authService.opendialog(LoginComponent);
  }

}
