import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/auth/auth.service';
import { Contact } from 'src/app/auth/contact.model';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  formProfile:any;
  regAuth:any;
  contact:Contact=new Contact({});
  constructor(private form:FormBuilder,private authService:AuthService,private _snackBar: MatSnackBar,private router:Router) { }

  ngOnInit(): void {
    this.contact=this.authService.decodeToken()
    this.formProfile=this.createFormProfile()
  }


  createFormProfile(): FormGroup{
    return this.form.group({
      username:new FormControl(this.contact.username||''),
      firstName: new FormControl(this.contact.firstName||'', Validators.required),
      lastName:new FormControl(this.contact.lastName||'',Validators.required),
      email:new FormControl(this.contact.email||'',Validators.required),
      password:new FormControl(this.contact.password||'',[Validators.required,Validators.min(5)]),
      adresse:new FormControl(this.contact.adresse||''),
      city:new FormControl(this.contact.city||''),
      pays:new FormControl(this.contact.pays||''),
      code_postal:new FormControl(this.contact.code_postal||''),
      roles:new FormControl(['Admin']),
      phone:new FormControl(this.contact.phone||'')
    })
      }


      profileUpdate():void{
        console.log(this.formProfile.value)
        this.contact=this.formProfile.value;
        this.regAuth=this.authService.updateUser(this.contact).then(res=>{
        if (!res.status){
          this.openSnackBar('vérifier les champs')
        }else {
          this.openSnackBar('Profile modifié avec succés')
          this.authService.closedialog()
          this.router.navigateByUrl('/Profile')
        }
      })
    
      }
    
    
    
      openSnackBar(action:any) {
        this._snackBar.open('',action,
          {
            duration:5000
          });
      }

      goback():void{

      }

}
