import {
  Component,
  OnInit
} from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroupDirective,
  NgForm,
  Validators
} from '@angular/forms';
import {
  ErrorStateMatcher
} from '@angular/material/core';
import {
  MatSnackBar
} from '@angular/material/snack-bar';
import {
  Router
} from '@angular/router';



import {
  AuthService
} from '../auth.service';
import { RegisterComponent } from '../register/register.component';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  contact: any;
  auth: any;
  user: any;
  actionAuth: string = '';
  loginForm = this.form.group({
    username: new FormControl('', [
      Validators.required

    ]),
    password: new FormControl('', [
      Validators.required

    ]),
  });
  matcher = new MyErrorStateMatcher();
  constructor(private form: FormBuilder, private authService: AuthService, private router: Router, private _snackBar: MatSnackBar) {}
  ngOnInit(): void {}
  login(): void {
    this.contact = this.loginForm.value;
    this.authService.login(this.contact).subscribe(
      res => {
        console.log(res)
        if (res.statut) {
          this.actionAuth = 'Email ou mot de passe incorrect'
          this.openSnackBar(this.actionAuth)
        } else {
          this.actionAuth = 'merci de connecté';
          localStorage.setItem('x-token', res.resultat); 
          this.authService.closedialog()
          this.router.navigateByUrl('/dashboard')
          this.openSnackBar(this.actionAuth)
          
        }
      })
  }


  openSnackBar(action: any) {
    this._snackBar.open('', action, {
      duration: 5000
    });
  }



  openRegister(): void {
    this.authService.closedialog()
    this.authService.opendialog(RegisterComponent);
  }


}